body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 20px;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input[type="text"],
input[type="password"] {
  height: 25px;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

ul {
  margin: 0;
  padding: 4px;
}

.label {
  position: absolute;
  float: left;
}

input[type="submit"] {
  margin-top: 10px;
  cursor: pointer;
  color: #4B74FF;
  background-color: white;
  border: #4B74FF 1px solid;
  border-radius: 30px;
  min-width: 80px;
  min-height: 30px;
}

input[type="submit"]:hover {
  background: #4B74FF;
  color: white;
}

.Signup,
.Login,
.Forgot-Password {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 2rem;
  width: 300px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.input-container {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin: 10px;
}

.button-container {
  display: flex;
  justify-content: center;
}

.form-switch {
  margin-top: 20px;
}

.switch-link {
  text-decoration: none;
  color: blue;
}

.logout button {
  cursor: pointer;
  position: fixed;
  top: 20px;
  right: 20px;
  color: #4B74FF;
  background-color: white;
  border: #4B74FF 1px solid;
  border-radius: 30px;
  min-width: 80px;
  min-height: 30px;
}

.logout button:hover {
  background: #4B74FF;
  color: white;
}

.Table {
  position: relative;
  height: 100vh;
  overflow: auto;
}

.Table-Container {
  position: absolute;
  top: 5%;
}

.Table-Header,
.Table-Options {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}

.add-member-btn {
  position: relative;
  cursor: pointer;
  color: white;
  background-color: #4B74FF;
  padding: 5px;
  text-align: center;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 50px;
  top: 5px;
  width: 140px;
  height: 20px;
  border-radius: 30px;
}

.plus-sign {
  position: absolute;
  top: 30%;
  margin-left: 3px;
  color: white;
}

.list-btn {
  position: relative;
  cursor: pointer;
  border: lightgrey 1px solid;
  min-width: 90px;
  padding: 5px;
  margin-right: 20px;
}
.drop-down{
  position: absolute;
  top: 25%;
  right: 5px;
}


.Table-Body {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.Data-Header {
  margin-top: 20px;
  padding: 5px;
  display: flex;
  flex-direction: row;
}

.Data-Header li {
  float: left;
  display: inline;
  list-style-type: none;
}

.value-container {
  max-width: 200px;
  margin-right: 180px;
  margin-left: 20px;
}

.t-data-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 1rem;
}

table {
  border-collapse: collapse;
  width: 60vw;
  position: relative;
}

th,
td {
  text-align: left;
  padding: 5px;
  font-size: 18px;
}

td {
  background-color: lightgray;
}

.c-list {
  position: absolute;
  display: flex;
  max-width: 500px;
  min-width: 200px;
  flex-direction: column;
  overflow: auto;
  border: lightgray 1px solid;
  background-color: white;
  z-index: 1;
}

.c-list li,
.s-list li {
  list-style-type: none;
  float: left;
}

.s-list {
  position: absolute;
  display: flex;
  max-width: 500px;
  min-width: 200px;
  left: 122px;
  flex-direction: column;
  overflow: auto;
  border: lightgray 1px solid;
  background-color: white;
  z-index: 1;
}

.Modal {
  position: fixed;
  z-index: 5;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.AddMember {
  position: fixed;
  text-align: left;
  padding: 20px;
  gap: 5px;
  left: 50%;
  top: 50%;
  width: 40vw;
  height: 40vh;
  transform: translate(-50%, -50%);
  background-color: white;
  z-index: 10;
  overflow: auto;
}
.mem-label {
  text-align: left;
}

.mem-btn {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: right;
  flex-wrap: wrap;
  gap: 30px;
}

#save-btn {
  color: white;
  background-color: #4B74FF;
  border-radius: 30px;
}